.works {
  background-color: crimson;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  .bgrArrowContainer {
    display: flex;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    position: relative;
    align-items: center;
    justify-content: center;

    &.left {
      right: calc(50vw - 120px);
    }

    &.right {
      left: calc(50vw - 120px);
    }

    .bgrNone {
      background-color: white;
      display: flex;
      height: 100%;
      flex: 5;
      align-items: center;
      justify-content: center;
    }

    .bgrArrow {
      display: flex;
      height: 100%;
      flex: 8;
      background-color: white;
      align-items: center;
      justify-content: center;

      .arrow {
        position: absolute;
        font-size: 130px;
        color: crimson;
        cursor: pointer;

        &.left {
          margin-right: 25px;
        }

        &.right {
          margin-left: 25px;
        }
      }
    }
  }

  .slider {
    height: 400px;
    display: flex;
    position: absolute;
    left: 0;
    transition: all 2s ease-out;

    .container {
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;

      .item {
        width: 1000px;
        height: 100%;
        background-color: white;
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: center;

        .left {
          flex: 4;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;

          .leftContainer {
            width: 90%;
            height: 70%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            margin-left: 50px;

            .phoneContainer {
              width: 60px;
              height: 60px;
              border-radius: 50%;
              background-color: crimson;
              overflow: hidden;
              justify-content: center;
              align-items: center;
              display: flex;

              .icon {
                font-size: larger;
              }
            }

            h2 {
              font-size: 30px;
            }

            p {
              font-size: 20px;
            }

            span {
              font-size: 17px;
              font-weight: 600;
              text-decoration: underline;
            }
          }
        }

        .right {
          flex: 8;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          overflow: hidden;

          img {
            width: 590px;
            transform: rotate(-10deg);
          }
        }
      }
    }
  }
}
