.app {
  height: 100vh;

  .sections {
    width: 100%;
    height: calc(100vh - 100px);
    position: relative;
    top: 100px;
    scroll-snap-type: y mandatory;
    scroll-behavior: smooth;
    scrollbar-width: none; //for firfox

    &::-webkit-scrollbar {
      display: none;
    }

    > * {
      width: 100vw;
      height: calc(100vh - 100px);
      scroll-snap-align: start;
    }
  }
}
